.intro {
    background-color: black;
    display: flex;

    .left {
        flex: 0.5;
        overflow: hidden;

        .imgcontainer {
            width: 700px;
            height: 700px;
            background-color: darkred;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            float: right;

            img {
                width: 72%;
            }
        }
    }

    .right {
        flex: 0.5;
        position: relative;

        .wrapper {
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            h1 {
                font-size: 60px;
                margin: 10px 0;
                color: white;
            }

            h2 {
                font-size: 30px;
                color: white;
            }

            h3 {
                font-size: 25px;
                color: white;

                span {
                    font-size: inherit;
                    color: crimson;
                }

                .ityped-cursor {
                    animation: blink 1s infinite;
                }

                @keyframes blink {
                    50% {
                        opacity: 1;
                    }

                    100% {
                        opacity: 0;
                    }

                }
            }
        }

        a {
            position: absolute;
            bottom: 10px;
            left: 50%;

            .arrowdown {
                width: 30px;
                animation: arrowBlink 2s infinite;
            }

            @keyframes arrowBlink {
                100% {
                    opacity: 0;
                }
            }
        }
    }
}