.works {
    background-color: darkred;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .arrow {
        height: 50px;
        position: absolute;
        cursor: pointer;

        &.left {
            left: 100px;
        }

        &.right {
            right: 100px;
        }
    }

    .slider {
        height: 350px;
        display: flex;
        position: absolute;
        left: 0vw;
        transition: all 1s ease-out;

        .container {

            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;

            .item {
                width: 700px;
                height: 100%;
                background-color: #fff;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;

                .left {
                    flex: 4;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .leftContainer {
                        width: 90%;
                        height: 70%;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;

                        .imgContainer {
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            background-color: lightcoral;
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            img {
                                width: 80%;
                            }
                        }

                        h2 {
                            font-size: 20px;
                        }

                        p {
                            font-size: 13px;
                            font-weight: 500;
                        }

                        span {
                            font-size: 15px;
                            font-weight: 700;
                            text-decoration: underline;
                            cursor: pointer;

                        }
                    }
                }

                .right {
                    flex: 8;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;

                    img {
                        width: 400px;
                        transform: rotate(-9deg);
                    }
                }
            }
        }
    }


}