@import "../../global.scss";


.topbar {
    width: 100%;
    height: 70px;
    background-color: black;
    color: white;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

    .wrapper {
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .left {
        display: flex;
        align-items: center;

        .logo {
            font-size: 30px;
            font-weight: 700;
            text-decoration: none;
            color: inherit;
            margin-right: 40px;
            margin-top: 8px;
        }

        .itemContainer {
            display: flex;
            align-items: center;
            margin-left: 30px;
            margin-top: 5px;

            .icon {
                font-size: 18px;
                margin-right: 5px;
            }

            span {
                font-size: 15px;
                font-weight: 500;
            }
        }
    }

    .right {

        .hamburger {
            width: 32px;
            height: 25px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            cursor: pointer;
        }

        span {
            width: 100%;
            height: 3px;
            background-color: white;
            transform-origin: left;
            transition: all 1s ease;
        }
    }

    &.active {
        background-color: $mainColor;
        color: white;

        .hamburger {
            span {
                &:first-child {
                    background-color: #fff;
                    transform: rotate(45deg);
                }

                &:nth-child(2) {
                    opacity: 0;
                }

                &:last-child {
                    background-color: #fff;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}